.content {
  padding: 2rem 0;
  background-color: rgba(255, 255, 255, 0.87);
  margin: auto;
  margin-top: 5%;
  margin-bottom: 5%;
  border-radius: 10px;
  box-shadow: 0px 1px 10px rgb(0, 0, 0);
  width: auto;
  text-align: center;
}

@media (min-width: 1200px) {
  .content {
    width: 63%;
  }
}

#nameError #numberError #emailError #confirmPasswordError {
  display: none;
}
.inner-contents {
  width: 100%;
  margin-left: 0px;
}

.content .contents .form-group,
.content .bg .form-group {
  margin-bottom: 0;
  border-bottom: none;
}


.content a {
  color: #888;
  text-decoration: underline;
}



.content .forgot-pass {
  font-size: 14px;
  text-decoration: none;
}
.content .forgot-pass:hover {
  font-weight: 600;
}
.social-login a {
  background-color: #888;
  text-decoration: none;
  text-align: center;
  color: rgb(0, 0, 0);
  margin-left: 1%;
  width: 40px;
  height: 37px;
  border-radius: 50%;
  display: inline-block;
}
.social-login a span {
  font-size: 20px;
  margin-top: 10px;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.social-login a:hover {
  background-color: rgba(136, 136, 136, 0.589);
}
.forgot-link {
  justify-content: center;
}
.forgot-link .caption {
  font-size: 14px;
  color: #899194;
}

.signup-link,
.login-link {
  text-align: center;
  font-weight: 400;
  text-decoration: underline;
}

.signup-link:hover,
.login-link:hover {
  text-align: center;
  font-weight: bolder;
  cursor: pointer;
}
.signup-frame {
  display: none;
}
#terms-label:hover {
  font-weight: 600;
}

.login-logo {
  max-width: 15vw;
  min-width: 150px;
  width: 100%;
  height: auto;
  margin-left: auto;
  margin-right: auto;
}

.align-logo {
  display: flex;
  align-items: center;
}

.adjust-padding {
  padding: 0px;
}

.image-input img {
  min-width: 80px;
  width: 80px;
  aspect-ratio: 1;
  object-fit: contain;
}
