.userProfilePage-card{
    box-shadow: 0px 5px 5px;
    padding: 30px;
    background-color: white;
    min-width: 350px;
    text-align: center;
    margin: 80px;
    margin-top: 50px;
}

#clickableDetailsSection:hover{
    cursor: pointer;
}
#userDetailsDisplaySection img{
    margin-bottom: 2vh;
    width: 200px;
    border: 2px solid gray;
    border-radius: 50%;
}
#userDetailsDisplaySection img{
    aspect-ratio: 1;
    object-fit: contain;
}
#UserNameExtract{
    margin-left: 1vh;
}

#userWebsiteDataDets div{
    border: 1px solid black;
    padding: 20px;
    margin: 10px 0px;
}
#userWebsiteDataDets div:hover{
    box-shadow: 0px 5px 10px rgb(0, 0, 0);
}
a{
    text-decoration: none;
}
@media screen and (max-width: 768px) {
    .userProfilePage-card{
        margin: 0px;
    }
}